import sanitizeHtml from 'sanitize-html'

const FILTER_ALL = {
	allowedTags: [],
	allowedAttributes: []
}
const FILTER_BASIC = sanitizeHtml.defaults
const FILTER_INLINE = {
	allowedTags: ['a', 'b', 'br', 'code', 'em', 'i', 'span', 'strike', 'strong', 'u'],
	allowedAttributes: {
		a: ['href', 'target'],
		span: ['style']
	},
	selfClosing: ['br'],
	allowedSchemes: ['ftp', 'http', 'https', 'mailto'],
	parser: {
		decodeEntities: true
	}
}
const FILTER_NONE = {
	allowedTags: false,
	allowedAttributes: false
}

export default {
	install(Vue, options) {

		/*const bind = function(el, binding) {
			if (Array.isArray(binding.value)) {
				el.innerHTML = sanitizeHtml(binding.value[1], binding.value[0])
			}
			else {
				if (binding.modifiers.all) {
					el.innerHTML = sanitizeHtml(binding.value, FILTER_ALL)
				}
				else if (binding.modifiers.inline) {
					el.innerHTML = sanitizeHtml(binding.value, FILTER_INLINE)
				}
				else if (binding.modifiers.none) {
					el.innerHTML = sanitizeHtml(binding.value, FILTER_NONE)
				}
				else {
					el.innerHTML = sanitizeHtml(binding.value)
				}
			}
		}

		Vue.directive('sanitize', {
			bind,
			update(el, binding, vnode, oldVnode) {
				if (binding.value != binding.oldValue) {
					bind(el, binding)
				}
			}
		})*/

		Vue.directive('sanitize', function(el, binding) {
			if (Array.isArray(binding.value)) {
				el.innerHTML = sanitizeHtml(binding.value[1], binding.value[0])
			}
			else {
				if (binding.modifiers.all) {
					el.innerHTML = sanitizeHtml(binding.value, FILTER_ALL)
				}
				else if (binding.modifiers.inline) {
					el.innerHTML = sanitizeHtml(binding.value, FILTER_INLINE)
				}
				else if (binding.modifiers.none) {
					el.innerHTML = sanitizeHtml(binding.value, FILTER_NONE)
				}
				else {
					el.innerHTML = sanitizeHtml(binding.value)
				}
			}
		})
	}
}



